const apiUrl={
    getNightTimes : 'set/getNightTimes'
,   setNightTimes : 'set/setNightTimes'
,   getEventCodeList : 'set/getEventCodeList'
,   setEventCodes : 'set/setEventCode'
,   setEventCodeOrderMod : "set/setEventCodeOrderMod"
,   delEventCode : "set/delEventCode"
}

const setting = {
    namespaced : "setting"
,   state : {
        tResData : {}       // 야간시간대 응답값
    }
,   mutations : {
        // 야간시간대 응답값 mutations
        SET_TIME_DATA : (state,res) => {
            state.tResData = res;
        }
    }
,   actions :{
        // 야간시간대 조회
        getNightTimes : async({commit}) =>{
            const result = await $axios.get(apiUrl.getNightTimes);
            return result;
        },    
        // 야간시간대 추가 및 수정
        saveSetTimes : async ({commit},parmas) => {            
            const result = await $axios.post(apiUrl.setNightTimes,parmas);                       
            //commit('SET_TIME_DATA',result)
            return result;
        },
        // 이벤트 항목 저장 
        saveEventCodes : async({commit}, parmas) =>{
            const result = await $axios.post(apiUrl.setEventCodes,parmas);            
            return result;
        },
        // 이벤트 항목 리스트 조회 
        getEventCodes : async({commit}, params)=>{
            var url = apiUrl.getEventCodeList +"/"+params.eventSeCode +"/"+params.eventTyCode ;
            const result = await $axios.get(url);
            return result;
        },
        // 이벤트 순번 수정
        setEventCodeOrderMod : async({commit},params) => {
            const result = await $axios.put(apiUrl.setEventCodeOrderMod,params);
            return result;
        },
        // 이벤트 항목 삭제
        delEventCode : async({commit},params) => {
            var url = apiUrl.delEventCode +"/"+params.eventSeCode +"/"+params.eventTyCode+"/"+params.eventDetailTyCode+"/"+params.codeOrdr ;            
            const result = await $axios.delete(url,params);
            return result;
        }

    }
}
export default setting;


