<template>
     <header class="header" v-if="$store.state.auth.isAuth">

           <div class="inner">
               
                <h1 class="logo">
                    <img src="/static/EXcode/static/images/ic_logo.png" alt="">
                </h1>

                <div class="hd_right">
                    <span class="user_name">{{ $store.state.auth.insttNm }}</span>  

                    <span class="user_name">{{ $store.state.auth.userNm }}</span>

                    <div class="input_box">
                        <button @click="headerLogOut()" class="btn btn_sm btn_secondary">
                            로그아웃
                        </button>
                    </div>    
                </div>
            </div>
       </header>
</template>
<script>

import { mapActions ,mapMutations} from 'vuex'

export default {
  methods: {
    ...mapActions('auth', ['authLogout']),    
    ...mapMutations('menu', ["SET_MENU"]),
    headerLogOut() {
      try {
        this.SET_MENU("")
        this.authLogout();        
      } catch (err) {
      } finally {
          this.$router.push({
          name: "login",
        });
      }
    }
  }
};
</script>


