<template>
      <div :class="['alert_wrap',isHidden]" ref="refAlert">
        <div class="alert_box">
          <div class="alert_hd">
              <p class="alert_title">{{title}}</p>
              <a href="javascript:void(0)" @click="fnClose()" class="btn_alert_close"><img src="/static/EXcode/static/images/ic_close.png" alt=""></a>
          </div>
          <div class="alert_bd">
              <div class="alert_desc_box" :class="['confirm'].includes(boxtype)?'':'non_btn'">
                  <div v-html="contents"></div>
                  <div class="input_box" v-if="['confirm'].includes(boxtype)">
                      <!-- 버튼의 수에 따라 button 태그를 늘리고 줄이시면 됩니다. -->
                      <button class="btn btn_primary btn_smd" @click="fnConfirm()">확인</button>
                      <button class="btn btn_primary_outline btn_smd" @click="fnCancel()">취소</button>
                  </div>
              </div>
          </div>
      </div>
  </div>
</template>
<script>
  export default {
    name: "confirmBox",
    props: {

    },
    data() {
      return {
        isState: false,
        setTitle : "",        
        message: '',
        boxtype: 'confirm',
        self: null,
        fn: null
      }
    },
    computed: {
      title() {
        if(['confirm'].includes(this.boxtype)) {
          return '확인'
        }else{
          return (this.setTitle == "") ? "경고" : this.setTitle
        }

      },
      contents() {
        return `<p>${this.message.split("\n").join("</p><p>")}</p>`
      },
      isHidden() {
        return this.isState ? "": 'hidden'
      }
    },
    methods: {
      fnConfirm () {
        this.isState = false
        this.message = ''
        return this.fn(true)
      },
      fnCancel() {
        this.fnClose()
      },
      fnOpen(fn) {
        this.isState = true
        this.fn = fn
      },
      fnClose() {
        this.isState = false
        this.message = ''
        // if(this.boxtype == "confirm")
        if (typeof this.fn == 'function')
        {
          return this.fn(false)
        }       
       
      }
    },
    destroyed() {
      this.message=""
      this.isState = false
      this.fn = null
    }
  }
</script>



