const URI = {
    CctvList: '/cctv/list',
    CctvInfo: '/cctv/manager/info',
    CctvPreset: '/cctv/manager/preset',
    CctvAlarmOff: '/cctv/manager/alarm-off',
    CctvOneAlarmOffSchedule: '/cctv/manager/alarm-off-schedule',
    AlarmOnExceptSchedule: '/cctv/manager/alarm-on-except-schedule',
    AlarmEventHistory: '/cctv/manager/alarm-event-history', 
    AlarmSetupHistory: '/cctv/manager/alarm-setup-history',
    CctvAlarmOffSchedule: '/cctv/alarm-off-schedule',
    CctvAnalysis: '/cctv/manager/analaysis'
}
import CMMCD from '@/utils/commonCode'
const cctv = {
    namespaced: 'cctv',
    state: {
      cctvList: [],
      cctvInfo: {},
      cctvPreset: [],
      cctvAlarmOffSchedule: null,
      cctvOneAlarmOffSchedule: [],
      alarmOnExceptSchedule: [],
      resAlarmOnExceptSchedule: null, 
      alarmEventHistory: [],
      ResultCctvOneAlarmOffSchedule: null,
      alarmSetupHistory: [],
      resCctvAnalysis: null,
      cctvAnalysis: {},
      resAlarmOff: null,
      errors:null
    },
    mutations: {
      GET_CCTV_LIST: (state, payload) => {
        state.cctvList = payload
      },
      GET_CCTV_INFO: (state, payload) => {
        state.cctvInfo = payload
      },
      GET_CCTV_PRESET: (state, payload) => {
        state.cctvPreset = payload
      },
      SET_CCTV_ALARM_OFF: (state, payload) => {
        state.resAlarmOff = payload
      },
      GET_CCTV_ALARM_OFF_SCHEDULE: (state, payload) => {
        state.cctvAlarmOffSchedule = payload
      },
      GET_CCTV_ONE_ALARM_OFF_SCHEDULE: (state, payload) => {
        state.cctvOneAlarmOffSchedule = payload
      },
      DELETE_CCTV_ONE_ALARM_OFF_SCHEDULE: (state, payload) => {
        state.ResultCctvOneAlarmOffSchedule = payload
      },
      GET_CCTV_ALARM_ON_EXCEPT_SCHEDULE: (state, payload) => {
        state.alarmOnExceptSchedule = payload
      },
      PUT_CCTV_ALARM_ON_EXCEPT_SCHEDULE: (state, payload) => {
        state.resAlarmOnExceptSchedule = payload
      },
      GET_ALARM_SETUP_HISTORY: (state, payload) => {
        state.alarmSetupHistory = payload
      },
      GET_ALARM_EVENT_HISTORY: (state, payload) => {
        state.alarmEventHistory = payload
      },
      PUT_CCTV_ANALYSIS: (state, payload) => {
        state.resCctvAnalysis = payload
      },
      GET_CCTV_ANALYSIS: (state, payload) => {
        for (let obj of payload) {
          state.cctvAnalysis = {...state.cctvAnalysis,  [obj.ANALS_CND_CODE]: obj.ANALS_AT }
        }
  
      },
      INIT_GET_CCTV_ANALYSIS: (state, payload) => {
        let analsSett = {}
        for (let obj of CMMCD.ANALS_CND_CODE) {
          analsSett = {...analsSett,  [obj.id]: 'Y' }
        }
        state.cctvAnalysis = analsSett
      },
      GET_ERROR: (state, payload) => {
        state.errors = payload
      },
      
    },
    actions: {
      // cctv 리스트
      getCctvList: ({commit}, payload) => {
        return new Promise((resolve, reject) => {
          $axios.get(URI.CctvList ,{params: payload})
          .then(res => {
            commit('GET_CCTV_LIST',res.data)
            resolve(res.data)
          })
          .catch(error => {
            commit('GET_ERROR',error)
            reject(error)
          })
        })          
          
      },
      // cctv 정보
      getCctvInfo: ({commit}, cctvId) => {
        return new Promise((resolve, reject) => {
          $axios.get(`${URI.CctvInfo}/${cctvId}`)
          .then(res => {
            const infoData = Array.isArray(res.data)?res.data[0]:res.data
            commit('GET_CCTV_INFO', infoData)
            resolve(infoData)
          })
          .catch(error => {
            commit('GET_ERROR', error)
            reject(error)
          })

        })
      },
      // cctv preset 
      getCctvPreset: ({commit}, cctvId) => {
        return new Promise((resolve, reject) => {
          $axios.get(`${URI.CctvPreset}/${cctvId}`)
            .then(res => {
              commit('GET_CCTV_PRESET', res.data)
              resolve(res.data)
            })
            .catch(error => {
              commit('GET_ERROR', error)
              reject(error)
            })
        })
      },
      postAlarmOff: ({commit}, params) => {
        return new Promise((resolve, reject) => {
          $axios.post(URI.CctvAlarmOff, params)
            .then(res => {
              commit('SET_CCTV_ALARM_OFF', res.data)
              resolve(res.data)
            })
            .catch(error => {
              commit('GET_ERROR', error)
              reject(error)
            })
        })
      },
      getCctvOneAlarmOffSchedule: ({commit}, cctvId) => {
        return new Promise((resolve, reject) => {
          $axios.get(`${URI.CctvOneAlarmOffSchedule}/${cctvId}`)
            .then(res => {
              commit('GET_CCTV_ONE_ALARM_OFF_SCHEDULE', res.data)
              resolve(res.data)
            })
        })
      },
      deleteAlarmOffSchedule: ({commit}, params) => {
        return new Promise((resolve, reject) => {
          $axios.put(`${URI.CctvOneAlarmOffSchedule}/${params.cctvId}`,params)
            .then(res => {
              commit('DELETE_CCTV_ONE_ALARM_OFF_SCHEDULE', res.data)
              resolve(res.data)
            })
        })
      },
      getAlarmOnExceptSchedule: ({commit}, cctvId) => {
        return new Promise((resolve, reject) => {
          $axios.get(`${URI.AlarmOnExceptSchedule}/${cctvId}`)
            .then(res => {
              commit('GET_CCTV_ALARM_ON_EXCEPT_SCHEDULE', res.data)
              resolve(res.data)
            })
        })
      },
      deleteAlarmOnExceptSchedule: ({commit}, params) => {
        return new Promise((resolve, reject) => {
          $axios.put(`${URI.AlarmOnExceptSchedule}/${params.cctvId}`, params)
            .then(res => {
              commit('PUT_CCTV_ALARM_ON_EXCEPT_SCHEDULE', res.data)
              resolve(res.data)
            })
        })
      },
      getAlarmOffSchedule: ({commit}, params) => {
        return new Promise((resolve, reject) => {
          $axios.get(`${URI.CctvAlarmOffSchedule}/${params.yyyymmdd}`)
            .then(res => {
              commit('GET_CCTV_ALARM_OFF_SCHEDULE', res.data)
              resolve(res.data)
            })
        })
      },
      getAlarmSetupHistory: ({commit}, cctvId) => {
        return new Promise((resolve, reject) => {
          $axios.get(`${URI.AlarmSetupHistory}/${cctvId}`)
          .then(res => {
            commit('GET_ALARM_SETUP_HISTORY', res.data)
            resolve(res.data)
          })
        })
      },
      getAlarmEventHistory: ({commit}, cctvId) => {
        return new Promise((resolve, reject) => {
          $axios.get(`${URI.AlarmEventHistory}/${cctvId}`)
          .then(res => {
            commit('GET_ALARM_EVENT_HISTORY', res.data)
            resolve(res.data)
          })
        })
      },
      getCctvAnalysis: ({commit}, cctvId) => {
        commit('INIT_GET_CCTV_ANALYSIS')
        return new Promise((resolve, reject) => {
          $axios.get(`${URI.CctvAnalysis}/${cctvId}`)
          .then(res => {
            commit('GET_CCTV_ANALYSIS', res.data)
            resolve(res.data)
          })
        })
      },
      putCctvAnalysis: ({commit}, params) => {
        return new Promise((resolve, reject) => {
          $axios.put(`${URI.CctvAnalysis}`, params)
          .then(res => {
            commit('PUT_CCTV_ANALYSIS', res.data)
            resolve(res.data)
          })

        })
      }
    },

   
}

export default cctv


