export default {
    methods: {
        $defConfirm(message, fn) {
            this.$root.$children[0].$refs.refConfirm.message = message
            this.$root.$children[0].$refs.refConfirm.boxtype='confirm'
            this.$root.$children[0].$refs.refConfirm.fnOpen(fn)
        },
        $info(message,title) {
            this.$root.$children[0].$refs.refAlert.message = message
            this.$root.$children[0].$refs.refAlert.boxtype='info'
            if(title != "" && title != undefined && title != null)
            {
                this.$root.$children[0].$refs.refAlert.setTitle=title
            }
            else
            {
                this.$root.$children[0].$refs.refAlert.setTitle=""                
            }
            this.$root.$children[0].$refs.refAlert.fnOpen()
        },
        $confirm(message){
            return new Promise((resolve, reject) => {
                this.$defConfirm(message, (v)=>{
                    return resolve(v) 
                })
            })
        }

    }
}


