const apiUrl = {
    getHisotryList : "history/getHisotryList"
}

const history = {
    namespaced :"his"
,   state  : {
        tResData : {}
    ,   intdUserList :[]
    }
,   mutations :{
        SET_HISTORY_DATA : (state,res) =>{
            state.tResData = res
        }
    ,   SET_INTD_USER_LIST : (state,res) =>{
            state.intdUserList = res
        }
    }
,   actions : {
        // 이력 목록 조회
        searchHisotryList : async({commit},param) =>{
            const result = await $axios.get(apiUrl.getHisotryList,{params:param});
            var data = result.data
            if(data.userList != null && data.userList != undefined && data.userList != "")
            {
                commit("SET_INTD_USER_LIST",data.userList)
            }
            return result;
        }
    }
}
export default history



