const apiUrl ={
        selEventList : "event/getEventList"
    ,   getEventPopData : "event/getEventPopData"
    ,   setEventInfo : "event/setEventInfo"
}
const event = {
    namespaced : "event"
,   state : {
        tResData : null
    ,   scanDrcCodeList : null
    ,   scanTyCodeList : null
    ,   eventSetList : null
    ,   pop : {
            cctvId : null
        ,   scanDt : null
        }
    }
,   mutations : {
        SET_EVENT_DATA : (state,res) =>{
            state.tResData = res;
        }
    ,   SET_SCAN_DRC_CODE_LIST : (state,res) => {
            state.scanDrcCodeList = res
        }
    ,   SET_SCAN_TY_CODE_LIST : (state,res) => {
            state.scanTyCodeList = res
        }
    ,   SET_SCAN_EVENT_POP : (state,res) =>
        { 
            state.pop.cctvId = res.info.CCTV_ID
            state.pop.scanDt = res.info.SCAN_DT
            //state.pop = res
            if(res.eventSetList && res.eventSetList.length > 0)
            {
                state.eventSetList = res.eventSetList
            }
        }
    }
,   actions : {
        getEventList : async ({commit},param) => {
            ///:scanStatus/:scanType/:scanDrc /:searchType            
            const result = await $axios.get(apiUrl.selEventList,{params : param});
            const data = result.data
            if(data.scanTyCodeList && data.scanTyCodeList.length > 0)
            {
                commit('SET_SCAN_TY_CODE_LIST',data.scanTyCodeList)
            }

            if(data.scanDrcCodeList && data.scanDrcCodeList.length > 0)
            {
                commit('SET_SCAN_DRC_CODE_LIST',data.scanDrcCodeList)
            }
            
            return result;
        }
    ,   getEventPopData : async({commit},param) =>{
            const result  =await $axios.get(apiUrl.getEventPopData,{params:param})
            commit('SET_SCAN_EVENT_POP',result.data)
            return result
        }
    ,   setEventInfo : async({commit},param) =>{
            const result = await $axios.put(apiUrl.setEventInfo,param);
            return result;
        }
    }
}

export default event;


