const BaseURL ="/set/"
export default [
    {
        path : `${BaseURL}timeSet`
    ,   name : "timeSet"      
    ,   component :() => import(`@/views/setting/NightTime.vue`)
    }
,   {
        path : `${BaseURL}eventSet`
    ,   name : "eventSet"      
    ,   component :() => import(`@/views/setting/EventTySet.vue`)
    }
]


