// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from '@/App'
import router from '@/router'
import { instance } from "@/utils/api";
import { store } from "@/store";    // 쿠키 설정 및 로그인
import gMaxin from "@/utils/gMaxin";
import VueCookies from 'vue-cookies'
import { ValidationProvider, extend, ValidationObserver } from "vee-validate" // vee-validate 로 부터 사용할 기능 import
import * as rules from "vee-validate/dist/rules"
for (let rule in rules) {
  // add the rule
  extend(rule, rules[rule])
}

extend( 'password',{
  getMessage: field => `${field} should include lower-case, 
  numeric digit, special chracter($@$!%*#?&).`,
  validate: value => {
    return /^.*(?=.*[a-zA-Z])(?=.*[0-9])(?=.*[$@$!%*#?&]).*$/.test(value)
  }
})

Vue.component("ValidationProvider", ValidationProvider)
Vue.component("ValidationObserver", ValidationObserver)

Vue.use(VueCookies)
//쿠키의 만료일은 7일이다. (글로벌 세팅)
Vue.$cookies.config("7d");
Vue.prototype.$axios = instance;
Window.prototype.$axios = instance;
Vue.config.productionTip = false;

Vue.mixin(gMaxin);

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



