
import VueCookies from 'vue-cookies'

const apiUrl = { 
    authLogin : "/auth/login",
    authLogout : "/auth/logout"
}

const auth = {
    namespaced : "auth",
    state : {
        // token: VueCookies.get("token") || '',
        userNm: VueCookies.get("userNm") || '',
        insttId: VueCookies.get("insttId") || '',
        userId:'',
        errorState: '',
        isAuth: true,
        insttNm:''
    },
    mutations: {
        setUser(state, userValue) {
            VueCookies.set("userNm", userValue.userNm);
            VueCookies.set("insttId", userValue.insttId);

            state.userNm = userValue.userNm
            state.userId = userValue.userId
            state.insttId = userValue.insttId
            state.insttNm = userValue.insttNm
            state.isAuth = true
        },
        logOut(state) {
            VueCookies.remove("userNm")
            VueCookies.remove("insttId")
            // VueCookies.keys().forEach(cookie => this.$cookies.remove(cookie));

            state.userNm = ""
            state.userId = ""
            state.insttId = ""
            state.insttNm = ""
            state.isAuth = false
        },
        cookiesRemoveAll(){
            VueCookies.keys().forEach(cookie => this.$cookies.remove(cookie));
        },
        setIsAuth(state){
            state.isAuth = false
        }
    },
    actions :{
        authLogin : async ({commit} , param) =>{
            /*{
                "userId": "srocmsw",
                "userPassword": "KzjSor+7Bcrod0hsUIsWphfFBSIXrtXMVrNVff7eUl8=",
                "userNm": "수원_국토관리사무소",
                "userTelNo": null,
                "userEmail": null,
                "useAt": "Y",
                "registerId": "daejeon",
                "registDt": "2020-11-23T04:12:20.000Z",
                "insttId": "1613168",
                "insttNm": "서울지방국토관리청"
            }*/
            const result = await $axios.post(apiUrl.authLogin,  param);
            commit('setUser',  result.data);
            return result;
        },
        authLogout : async({commit})=>{
            const result = await $axios.get(apiUrl.authLogout);
            commit('logOut')
            return;
        },
        setIsAuth : async ({commit}) =>{
            commit('setIsAuth')
            return;
        }
    }
}

export default auth;


