<template>
  <div class="wrap"> 
    <!-- Header -->
    <Header></Header>                   
    <!-- leftBar -->
    <LeftBar></LeftBar> 
    <!-- contents -->
    <router-view/>
    <Confirm ref="refConfirm"/>
    <Confirm ref="refAlert" />
  </div>
</template>

<script>
import Header from "@/views/common/Header.vue"
import LeftBar from "@/views/common/LeftBar.vue"
import Confirm from "@/components/common/Confirm.vue"
import { mapActions, mapMutations } from 'vuex' // mapGetters를 추가한다

export default {
    name: 'App',
    created()
    {  
      var curPath = window.location.pathname;
      // 브라우저에서 login
      if(curPath.indexOf("login") != -1)
      {   
          // 로그인 이후에 브라우저에서 했을때 강제 로그아웃
          if(this.isNull(this.$store.state.auth.insttId) ==false)
          {
            this.authLogout();
            return;
          }        
      }
      
      // 로그인 여부 체크
      if(this.isNull(this.$store.state.auth.insttId))
      { 
          // 로그인 x
          this.SET_MENU("")
          this.setIsAuth()          
      }
      else
      {
          // 로그인 이후 새로고침시
          if(this.isNull(this.$store.state.menu.selectMenu))
          {  
              var menuNM = "";
              if(curPath.indexOf("dashboard") != -1)
              {
                  menuNM = "dashboard"
              }
              else if(curPath.indexOf("event") != -1)
              {
                  menuNM ="eventList"
              }   
              else if(curPath.indexOf("stats") != -1)
              {
                  menuNM ="stats"
              }
              else if(curPath.indexOf("cctv") != -1)
              {
                  menuNM="cctv"
              }
              else if(curPath.indexOf("history") != -1)
              {
                  menuNM="history"
              }
              else if(curPath.indexOf("timeSet") != -1)
              {
                  menuNM="timeSet"
              }

              this.SET_MENU(menuNM)           
          }
      }
      
    },        
    methods :{
      ...mapMutations('menu', ["SET_MENU"]),
      ...mapActions('auth', ['setIsAuth','authLogout'])
    ,  isNull(str)
      {
        if(str == undefined || str == "" || str == null)
        {
          return true
        }
        else
        {
          return false
        }
      }
    },
    components :{
      Header,
      LeftBar,
      Confirm
    },
}
</script>



