<template>
    <aside class="left_nav" v-if="$store.state.auth.isAuth">
           <nav>
                <div class="nav_box left_box">
                    <a href="javascript:void(0)" @click="movePage('dashboard')" class="nav_item"><img src="/static/EXcode/static/images/ic_dashboard.png" alt=""></a>
                    <a href="javascript:void(0)" @click="movePage('eventList')" class="nav_item"><img src="/static/EXcode/static/images/ic_event.png" alt=""></a>
                    <a href="javascript:void(0)" @click="movePage('stats')" class="nav_item"><img src="/static/EXcode/static/images/ic_circle.png" alt=""></a>
                    <a href="javascript:void(0)" @click="movePage('cctv')" class="nav_item"><img src="/static/EXcode/static/images/ic_cctv.png" alt=""></a>
                    <a href="javascript:void(0)" @click="movePage('history')" class="nav_item"><img src="/static/EXcode/static/images/ic_assignment.png" alt=""></a>
                    <a href="javascript:void(0)" @click="movePage('timeSet')" class="nav_item"><img src="/static/EXcode/static/images/ic_settings.png" alt=""></a>
                </div>
                <!-- 현재 진입해있는 페이지의 menu_box > nav_item에 클래스명 active  -->
                <div class="nav_box menu_box">
                    <a href="javascript:void(0)" @click="movePage('dashboard')" :class="['nav_item', isActive('dashboard')]">현황판</a>
                    <a href="javascript:void(0)" @click="movePage('eventList')" :class="['nav_item', isActive('eventList')]">이벤트</a>
                    <a href="javascript:void(0)" @click="movePage('stats')" :class="['nav_item', isActive('stats')]">통계</a>
                    <a href="javascript:void(0)" @click="movePage('cctv')" :class="['nav_item', isActive('cctv')]">CCTV</a>
                    <a href="javascript:void(0)" @click="movePage('history')" :class="['nav_item', isActive('history')]">이력</a>
                    <a href="javascript:void(0)" @click="movePage('timeSet')" :class="['nav_item', isActive('timeSets')]">설정</a>
                </div>
           </nav>
       </aside> 
</template>
<script>
    import { mapMutations, mapState } from 'vuex'

    export default {
        data() {
            return {

            }
        },
        computed:{
            ...mapState('menu', ["selectMenu"])
        },
        mounted() {

            
        },
        methods: {
            ...mapMutations('menu', ["SET_MENU"]),
            movePage(menuName)
            { 
                event.preventDefault()
                try {
                    var bfMenuName = this.$store.state.menu.selectMenu;
                    this.SET_MENU(menuName)
                    if(bfMenuName == menuName) return;
                    this.$router.push({ name: menuName })
                }catch(e) {
                    console.log(e)
                }
            },
            isActive(nm) {
                return (this.selectMenu || 'dashboard') == nm ? 'active': ''
            }
        }
    }

</script>


