import Vue from "vue"
import axios from 'axios'
import { store } from '../store'
import qs from "qs"
import router from '../router'


const vue = new Vue({store})

const instance = axios.create({
    baseURL: '/api/',
    timeout: 30000,
    headers : {
        header : "application/json; charset=utf-8"
    }
})
// ex $axios.get("url", {param}) -> url?querystring 으로 자동 변경
instance.defaults.paramsSerializer = params => {
    return qs.stringify(params)
}
// 요청전
instance.interceptors.request.use(
    function(config) {
        // 요청전 처리코드 //
        config.headers.Authorization = store.state.token // '{{ token }}'        
        return config
    },
    function(error) {
        return error
        //return Promise.reject(error)
    }
)
// 응답 완료후 처리
instance.interceptors.response.use(
    function(response) {        
        // 처리 코드 //
        return response
    }, 
    function(error) {
        // 응답오류        
        const errRes = error.response;
        if(errRes.status == 403) {
            if(errRes.data == "N") {
                alert("아이디 또는 비밀번호가 잘못 입력 되었습니다.");
                return;
            }
            vue.$store.commit("auth/logOut")
            return router.push({name:'login'});
        } else if(errRes.status != 500) {
            return error.response
        } else {
            return error
        }
        
        //return Promise.reject(error)
    }
)

export { instance }



