import Vue from 'vue'
import Router from 'vue-router'
import sample from "@/router/sample/sample";
import stats from "@/router/statistical/statistical"
import login from "@/router/login/login"
import dashboard from "@/router/dashboard/dashboard"
import set from "@/router/set/setRouter"
import event from "@/router/event/eventRouter"
import history from "@/router/history/historyRouter"

Vue.use(Router)

const router = new Router({
  mode : "history",
  routes: [
    {
      path: '/',
      name: 'main',
      component: () => import("@/views/dashboard/dashboard.vue"),
      // meta: {auth:true}
      // beforeEnter: initSearch,
    },
    // 페이지 없을때 출력화면(필요여부에 따라  주석처리!)
    {
      path:"*",
      component: ()=> import("@/views/common/NotFoundPage.vue")
    },
    {
      path: '/cctv',
      name: 'cctv',
      component: () => import("@/views/cctv/Cctv.vue")
    },
    ...stats,
    ...login,
    ...dashboard,
    ...set,  // 설정
    ...event, // 이벤트
    ...history,  // 이력
  ]
})

// 로그인 인증 처리, 각자 개별시엔 잠금
// router.beforeEach((to, from, next) => {
//   if (to.meta.auth && ! store.state.auth.isAuth) {
//     console.log('인증이 필요합니다');
//     next('/login');
//     return;
//   }
//   next();
// });

export default router;


